'use client'

import { useState } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Profile } from '@/lib/data'

interface SwipeCardProps {
  profile: Profile
  onLike: (profileId: string) => void
  onDislike: (profileId: string) => void
  isActive: boolean
}

export function SwipeCard({ profile, onLike, onDislike, isActive }: SwipeCardProps) {
  const [isAnimating, setIsAnimating] = useState(false)
  const [animationDirection, setAnimationDirection] = useState<'left' | 'right' | null>(null)

  const handleAction = (action: 'like' | 'dislike') => {
    setIsAnimating(true)
    setAnimationDirection(action === 'like' ? 'right' : 'left')
    
    setTimeout(() => {
      if (action === 'like') {
        onLike(profile.id)
      } else {
        onDislike(profile.id)
      }
      setIsAnimating(false)
      setAnimationDirection(null)
    }, 300)
  }

  return (
    <div className={`absolute inset-0 transition-all duration-300 ${
      isActive ? 'z-10' : 'z-0 scale-95 opacity-50'
    } ${
      isAnimating 
        ? animationDirection === 'right' 
          ? 'translate-x-full rotate-12' 
          : '-translate-x-full -rotate-12'
        : ''
    }`}>
      <Card className="h-full w-full max-w-sm mx-auto overflow-hidden shadow-lg">
        <div className="relative h-2/3">
          <img
            src={profile.imageUrl}
            alt={profile.name}
            className="w-full h-full object-cover"
            onError={(e) => {
              const target = e.target as HTMLImageElement
              target.src = 'https://images.unsplash.com/photo-1494790108755-2616b612b786?w=400&h=400&fit=crop&crop=face'
            }}
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent" />
          <div className="absolute bottom-4 left-4 text-white">
            <h2 className="text-2xl font-bold">{profile.name}</h2>
            <p className="text-lg opacity-90">{profile.age} years old</p>
          </div>
        </div>
        
        <CardContent className="p-4 h-1/3 flex flex-col justify-between">
          <div className="space-y-3">
            <p className="text-sm text-muted-foreground line-clamp-3">
              {profile.description}
            </p>
            
            <div className="flex flex-wrap gap-1">
              {profile.interests.slice(0, 3).map((interest, index) => (
                <Badge key={index} variant="secondary" className="text-xs">
                  {interest}
                </Badge>
              ))}
            </div>
          </div>
          
          <div className="flex gap-3 mt-4">
            <Button
              variant="outline"
              className="flex-1 h-12 text-base font-medium border-2 hover:bg-destructive hover:text-white hover:border-destructive"
              onClick={() => handleAction('dislike')}
              disabled={isAnimating}
            >
              Pass
            </Button>
            <Button
              className="flex-1 h-12 text-base font-medium bg-green-600 hover:bg-green-700"
              onClick={() => handleAction('like')}
              disabled={isAnimating}
            >
              Like
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
