'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { SwipeCard } from '@/components/SwipeCard'
import { useAuth } from '@/context/AuthContext'
import { dummyProfiles, Profile } from '@/lib/data'

export default function HomePage() {
  const { user, logout } = useAuth()
  const router = useRouter()
  const [profiles, setProfiles] = useState<Profile[]>([])
  const [currentIndex, setCurrentIndex] = useState(0)
  const [matches, setMatches] = useState<string[]>([])

  useEffect(() => {
    if (!user) {
      router.push('/login')
      return
    }
    
    // Filter out the current user's profile
    const availableProfiles = dummyProfiles.filter(profile => profile.id !== user.id)
    setProfiles(availableProfiles)
  }, [user, router])

  const handleLike = (profileId: string) => {
    setMatches(prev => [...prev, profileId])
    setCurrentIndex(prev => prev + 1)
    
    // Simulate match notification
    const likedProfile = profiles.find(p => p.id === profileId)
    if (likedProfile) {
      // In a real app, you'd check if the other user also liked you
      console.log(`You liked ${likedProfile.name}!`)
    }
  }

  const handleDislike = (profileId: string) => {
    setCurrentIndex(prev => prev + 1)
  }

  const handleLogout = () => {
    logout()
    router.push('/')
  }

  if (!user) {
    return null
  }

  const currentProfile = profiles[currentIndex]
  const nextProfile = profiles[currentIndex + 1]

  return (
    <div className="min-h-screen bg-gradient-to-br from-background to-muted">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b bg-background/80 backdrop-blur-sm">
        <div>
          <h1 className="text-xl font-bold">Dating App</h1>
          <p className="text-sm text-muted-foreground">Welcome, {user.name}</p>
        </div>
        <div className="flex gap-2">
          <Button
            variant="outline"
            size="sm"
            onClick={() => router.push('/chat')}
          >
            Chat
          </Button>
          <Button
            variant="outline"
            size="sm"
            onClick={() => router.push('/profile')}
          >
            Profile
          </Button>
          <Button
            variant="ghost"
            size="sm"
            onClick={handleLogout}
          >
            Logout
          </Button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 p-4">
        {currentIndex >= profiles.length ? (
          <div className="flex flex-col items-center justify-center h-96 text-center space-y-4">
            <div className="text-6xl">ðŸŽ‰</div>
            <h2 className="text-2xl font-bold">No more profiles!</h2>
            <p className="text-muted-foreground max-w-md">
              You've seen all available profiles. Check back later for new people to connect with!
            </p>
            <div className="space-y-2">
              <p className="text-sm font-medium">You liked {matches.length} profiles</p>
              <Button onClick={() => router.push('/chat')}>
                View Your Matches
              </Button>
            </div>
          </div>
        ) : (
          <div className="relative h-[600px] max-w-sm mx-auto">
            {/* Next card (background) */}
            {nextProfile && (
              <SwipeCard
                key={nextProfile.id}
                profile={nextProfile}
                onLike={handleLike}
                onDislike={handleDislike}
                isActive={false}
              />
            )}
            
            {/* Current card (foreground) */}
            {currentProfile && (
              <SwipeCard
                key={currentProfile.id}
                profile={currentProfile}
                onLike={handleLike}
                onDislike={handleDislike}
                isActive={true}
              />
            )}
          </div>
        )}

        {/* Stats */}
        {currentIndex < profiles.length && (
          <div className="text-center mt-6 space-y-2">
            <p className="text-sm text-muted-foreground">
              {currentIndex + 1} of {profiles.length} profiles
            </p>
            <div className="w-full max-w-sm mx-auto bg-muted rounded-full h-2">
              <div 
                className="bg-primary h-2 rounded-full transition-all duration-300"
                style={{ width: `${((currentIndex + 1) / profiles.length) * 100}%` }}
              />
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
